ks-page-compare { 
    display: block;
    background-color: #ffffff;
}

ks-page-compare .compare-products { 
    display: flex;
    align-items: center;
    padding: 20px 20px 0px 20px;
}

ks-page-compare .compare-attributes { 
    padding: 0px 20px 20px 20px;
}

ks-page-compare .compare-products,
ks-page-compare .compare-attributes {
    width: 100%;
    max-width: 1400px;
    box-sizing: border-box;
    margin: auto;
    background-color: white;
}

ks-page-compare .compare-products > * { 
    flex: 1;
}

ks-page-compare .compare-products .product { 
    text-align: center;
    position: relative;
}

ks-page-compare .compare-products .product a { 
    color: #151515;
    text-decoration: none;
}

ks-page-compare .compare-products .product ks-img3 { 
    max-width: 120px;
    max-height: 120px;
    margin: auto;
}

ks-page-compare .compare-products .product ks-button { 
    position: absolute;
    top: 0;
    right: 0;
    opacity: 0.3;
}

ks-page-compare .compare-products .add { 
    display: flex;
    justify-content: center;
    align-items: center;
}

ks-page-compare .compare-products .add ks-button { 
    opacity: 0.5;
}

ks-page-compare .compare-products * ks-input-check { 
    margin-top: 10px;
    margin-bottom: 0px;
}

ks-page-compare .compare-products * h1 { 
    margin: 0px;
}

ks-page-compare .compare-products * h2 { 
    margin: 10px 0px 0px 0px;
}

ks-page-compare .compare-products * p { 
    margin: 5px 0px 0px 0px;
}

ks-page-compare .compare-attributes .category {
    position: relative;
}

ks-page-compare .compare-attributes .category > ks-icon { 
    top: 5px;
    right: 15px;
    position: absolute;
}

ks-page-compare .compare-attributes .category h3 { 
    border-bottom: 1px solid #e7e7e7;
    padding: 5px;
    margin-bottom: 0px;
    margin-top: 20px;
    font-weight: 700;
}

ks-page-compare .compare-attributes .category.interactable h3 { 
    cursor: pointer;
    transition: background-color 0.3s ease;
}

ks-page-compare .compare-attributes .category.interactable h3:hover { 
    background-color: #f2f2f2;
}

ks-page-compare .compare-attributes .category.interactable h3:active { 
    background-color: #e6e6e6;
}

ks-page-compare table { 
    width: 100%;
    table-layout: fixed;
    border-spacing: 0px;
}

ks-page-compare table th,
ks-page-compare table td { 
    text-align: center;
    width: 25%;
    padding: 5px;
    border-bottom: 1px solid #e7e7e7;
}

ks-page-compare table th { 
    text-align: left;
    font-family: var(--font-emphasis);
}

ks-page-compare table tr:nth-child(even) td { 
    background-color: #f2f2f2;
}

ks-page-compare table tr td:not(:last-child), 
ks-page-compare table tr th:not(:last-child)  { 
    border-right: 1px solid #e7e7e7;
}

ks-page-compare[differences] .identical { 
    display: none;
}

ks-page-compare table.hidden {
    display: none;
}

@media screen and (min-width: 960px) {
    ks-page-compare .compare-products {
        padding: 50px 50px 0px 50px;
    }

    ks-page-compare .compare-attributes {
        padding: 0px 50px 20px 50px;
    }
}

@media screen and (max-width: 960px) {
    ks-page-compare .compare-products .product ks-img3 {
        max-width: 90px;
    }
}

@media screen and (max-width: 640px) {
    ks-page-compare .compare-products { 
        display: block;
        margin-bottom: 20px;
    }

    ks-page-compare .compare-products .product a {
        display: flex;
        align-items: center;
        text-align: left;
    }

    ks-page-compare .compare-products .add {
        display: block;
        margin-top: 15px;
    }

    ks-page-compare .compare-products .product .text {
        width: 100%;
    }

    ks-page-compare .compare-products .product ks-button {
        position: static;
    }

    ks-page-compare .compare-products .product ks-img3 {
        max-width: 60px;
        margin: 0px 10px 0px 0px;
    }

    ks-page-compare .compare-attributes .category h3 {
        padding: 12px 5px;
    }

    ks-page-compare .compare-attributes .category > ks-icon {
        top: 12px;
    }

    ks-page-compare .compare-attributes .category td ks-icon {
        line-height: 15px !important;
    }

    ks-page-compare .compare-attributes .category td ks-icon svg {
        stroke-width: 3;
        width: 15px;
        height: 15px;
    }

    ks-page-compare .compare-products .info { 
        width: 100%;
        margin-bottom: 20px;
    }

    ks-page-compare .compare-attributes {
        padding: 0px 10px 10px 10px;
    }

    ks-page-compare .compare-attributes .category {
        margin-bottom: 20px;
    }

    ks-page-compare table tr {
        position: relative;
    }

    ks-page-compare table th { 
        position: absolute;
        font-size: 12px;
        width: 100%;
        border-bottom: none;
        top: 2px;
    }

    ks-page-compare table td { 
        width: 33.33%;
        padding: 25px 5px 5px 5px;
        font-size: 14px;
        border-right: none !important;
    }
}

@media screen and (max-width: 420px) {
    ks-page-compare .compare-products > div:nth-child(n+4),
    ks-page-compare table td:nth-child(n+4) {
        display: none;
    }

    ks-page-compare table td { 
        width: 50%;
    }
}