import { Component, h, Prop, State } from '@stencil/core';
import { compare, CompareData } from "../../../global/data/compare";
import priceFormat from '../../../global/functions/price';
import Cookies from 'js-cookie';

declare global { interface Window { dataLayer: any[] }}
@Component({
    tag: 'ks-page-compare',
    styleUrl: 'compare.css'
})
export class PageCompare {
    @Prop() skipbase: boolean;
    @Prop() commonData: string;
    @Prop() commonDynamicData: string;

    @Prop() compareData: string;
    @Prop({reflect: true, mutable: true}) differences: boolean = false;

    @State() hidden: number[] = [];

    componentWillLoad() {
        const compareDataElement = document.getElementById(this.compareData);
		const compareData = JSON.parse(compareDataElement.innerHTML) as CompareData;

        Object.keys(compareData).map(key => {
			compare.set(key as keyof CompareData, compareData[key]);
		});
    }

    removeProduct(id: string) {
        const index = compare.get("products").findIndex(product => product.id == id);
        if(index == -1) return;    

        let products = compare.get("products");
        products.splice(index, 1);

        compare.set("products", [...products]);

        this.removeProductCookie(id)

        if(products.length == 0) this.addProduct();
        else window.location.href = "/porownaj-" + products.map(item => item.id).join("-") + ".html";
    }

    removeProductCookie(id: string) {
        const state = JSON.parse(Cookies.get('product-comparison'));
        const index = state?.findIndex(product => product.id == id); 
        if(index == -1) return;

        state.splice(index, 1);

        Cookies.set('product-comparison', JSON.stringify(state), { expires: 60 });
    }
    
    addProduct() {
        window.location.href = compare.get("addProductLink");
    }

	render() {
        const products = compare.get("products");
        let product_ids = products.map(product => product.id);

        if(product_ids.length < 3)
            product_ids = [...product_ids, ...(new Array(3-product_ids.length))];

        let attributes = new Map<string, Map<string, Map<string, string>>>();

        products.forEach(product => {
            product.attributeCategories.forEach(category => {
                if(!attributes.has(category.name))
                    attributes.set(category.name, new Map());

                category.attributes.forEach(attribute => {
                    const categoryMap = attributes.get(category.name);

                    if(!categoryMap.has(attribute.name))
                        categoryMap.set(attribute.name, new Map());

                    categoryMap.get(attribute.name).set(product.id, attribute.value);
                });
            })
        });

		return <ks-page-base skipbase={this.skipbase} commonData={this.commonData} commonDynamicData={this.commonDynamicData}>
            <div class="compare-products">
                <div class="info">
                    <h1>Porównaj modele</h1>
                    <ks-input-check label="Pokaż tylko różnice" onChange={e => this.differences = (e.target as HTMLInputElement).checked}/>
                </div>
                {product_ids.map(id => {
                    const product = products.find(product => product.id == id);

                    if(!product) return <div class="add">
                        <ks-button border round icon="plus" onClick={()=>this.addProduct()}/>
                    </div>

                    return <div  class="product">
                        <a href={product.link}>
                            <ks-img3 image={product.image} fit='contain'/>
                            <div class="text">
                                <h2>{product.name}</h2>
                                <p>{product.description}</p>
                            </div>
                        </a>
                        <ks-button border round narrower icon="x" onClick={()=>this.removeProduct(product.id)} />
                    </div>
                })}
            </div>
            <div class="compare-attributes">
                <div class="category">
                    <h3>Informacje Ogólne</h3>
                    <table><tbody>
                        <tr>
                            <th>Kategoria</th>
                            {product_ids.map(id => 
                                <td>{products.find(product => product.id == id)?.category}</td>
                            )}
                        </tr>
                        <tr>
                            <th>Cena</th>
                            {product_ids.map(id => 
                                <td>{priceFormat(products.find(product => product.id == id)?.price)}</td>
                            )}
                        </tr>
                        <tr>
                            <th>Czas dostawy</th>
                            {product_ids.map(id => 
                                <td>{products.find(product => product.id == id)?.shipping}</td>
                            )}
                        </tr>
                    </tbody></table>
                </div>

                {[...attributes.entries()].map(([category_name, category], index) => 
                    <div class={"category interactable " + (this.same_attributes_in_category(category, product_ids) ? "identical " : "")}>

                        <h3 onClick={()=>this.toggle_hidden(index)}>{category_name}</h3>
                        <ks-icon name={this.hidden.includes(index) ? "chevron-down" : "chevron-up"} />
                        <table class={(this.hidden.includes(index) ? "hidden" : "")}>
                            <tbody>

                            {[...category.entries()].map(([attribute_name, attribute]) =>
                                <tr class={this.same_attributes(attribute, product_ids) ? "identical" : ""}>
                                    <th>{attribute_name}</th>
                                    {product_ids.map(id => 
                                        <td>{attribute.has(id) ? 
                                            (attribute.get(id) == "true" ?
                                                <ks-icon name="check"/> : attribute.get(id)
                                            ) : "-"}
                                        </td>
                                    )}
                                </tr>
                            )}

                            </tbody>
                        </table>
                    </div>
                )}
            </div>
        </ks-page-base>;
	}

    toggle_hidden(index: number) {
        const item_index = this.hidden.indexOf(index);

        if(item_index != -1) {
            const array = this.hidden;
            array.splice(item_index, 1);
            this.hidden = [...array];
        }

        else this.hidden = [...this.hidden, index];
    }

    same_attributes(attribute: Map<string, string>, product_ids: string[]) {
        product_ids = product_ids.filter(item => item !== undefined);
        return [...attribute.values()].every((v, _, arr) => v === arr[0]) && attribute.size == product_ids.length;
    }

    same_attributes_in_category(category: Map<string, Map<string, string>>, product_ids: string[]) {
        return [...category.entries()].map(([_, attribute]) => this.same_attributes(attribute, product_ids)).every(v => v == true);
    }
}




