import { createStore } from "@stencil/store";

export const compare = createStore({} as CompareData);

export interface CompareData {
    addProductLink: string;
    products: CompareDataProduct[];
}

export interface CompareDataProduct {
    id: string;
    image: string;
    name: string;
    link: string;
    description: string;
    category: string;
    price: number;
    shipping: string;

    attributeCategories: CompareDataAttributeCategory[];
}

export interface CompareDataAttributeCategory {
    name: string;
    attributes: CompareDataAttribute[]
}

export interface CompareDataAttribute {
    name: string;
    value: string;
}